<template>
    <li v-if="isSubmenu" class="dropdown-submenu">
      <span class="dropdown-item" >{{ item.name }}</span>
      <ul class="dropdown-menu">
          <SubMenu
            v-for="(child, index) in item.children"
            :key="index"
            :item="child"
          ></SubMenu>
      </ul>
    </li>
    <li v-else class="dropdown-item"><router-link class="text-decoration-none" :to="item.url">{{ item.name }}</router-link></li>
</template>

<script>
export default {
  name: "SubMenu",
  props: {
    item: Object,
  },
  computed: {
    isSubmenu: function () {
      return this.item.children && this.item.children.length;
    },
  }
};
</script>

