<template>
  <!-- https://bootstrap-vue.org/docs/components/dropdown#dropdown-supported-sub-components -->
  <!-- Note: Nested sub-menus are not supported. -->
  <b-navbar sticky toggleable="lg" type="light" variant="info">
    <!-- <b-navbar-brand href="#">NavBar</b-navbar-brand> -->
    <b-navbar-brand href="javascript:void(0)">
      <b-icon-arrow-bar-right
        v-b-toggle.sidebar
        v-b-tooltip.hover.v-light.bottomright="'側邊選單'"
      ></b-icon-arrow-bar-right>
    </b-navbar-brand>
    <b-sidebar id="sidebar" shadow>
      <template v-slot:title>
        <span class>書目分類</span>
      </template>
      <template v-slot:default="{ hide }">
        <div class="list-group">
          <TreeMenu :listData="list" @hide="hide"></TreeMenu>
        </div>
      </template>
    </b-sidebar>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <b-nav-item to="/">
          <font-awesome-icon :icon="['fas', 'home']" />&nbsp;回首頁
        </b-nav-item>
        <b-nav-item v-if="isAuthenticated" @click="logout">
          <font-awesome-icon :icon="['fas', 'sign-out-alt']" />&nbsp;登出
        </b-nav-item>
        <b-nav-item to="/login" v-else>
          <font-awesome-icon :icon="['fas', 'sign-in-alt']" />&nbsp;登入
        </b-nav-item>
        <b-nav-item :to="{ path: '/query' }">
          <font-awesome-icon :icon="['far', 'list-alt']" />&nbsp;查詢訂單
        </b-nav-item>
        <b-nav-item to="/shoppingcart">
          <div @mouseenter="refresh">
            <b-icon-cart-fill
              v-b-tooltip.hover.v-light.bottomright="'購物車'"
            ></b-icon-cart-fill>
            <b-badge
              pill
              variant="light"
              class="align-top"
              style="margin-top: -5px; margin-left: -5px"
              >{{ productItems }}</b-badge
            >
          </div>
        </b-nav-item>
        <b-nav-item v-if="isAuthenticated">
          {{ getUserChName }} 您好
        </b-nav-item>
        <!-- <b-nav-item href="#">Link</b-nav-item> -->
        <!-- <b-nav-item href="#" disabled>Disabled</b-nav-item> -->
      </b-navbar-nav>

      <!-- Right aligned nav items -->
      <!-- <b-navbar-nav class="ml-auto"> -->
      <!-- <DropDownMenu
          :menuData="menuData"
          :dropdownBtnClass="btnClass"
          :btnID="btnID"
        ></DropDownMenu>
        <b-nav-item-dropdown text="Lang" right>
          <b-dropdown-item to="#">EN</b-dropdown-item>
          <b-dropdown-item to="#">ES</b-dropdown-item>
          <b-dropdown-item to="#">RU</b-dropdown-item>
          <b-dropdown-item to="#">FA</b-dropdown-item>
        </b-nav-item-dropdown> -->

      <!-- <b-nav-item-dropdown right>
          <template v-slot:button-content>
            <em>User</em>
          </template>
          <b-dropdown-item to="#">Profile</b-dropdown-item>
          <b-dropdown-item to="#">Sign Out</b-dropdown-item>
        </b-nav-item-dropdown> -->

      <!-- <b-nav-form>
          <b-form @submit="onSubmit">
            <b-form-input
              required
              v-model="form.search"
              id="input-search"
              size="sm"
              class="mr-sm-1"
              placeholder
            ></b-form-input>
            <b-button
              size="sm"
              variant="light"
              class="my-1 my-sm-0"
              type="submit"
              >搜尋</b-button
            >
          </b-form> -->
      <!-- </b-nav-form> -->
      <!-- </b-navbar-nav> -->
    </b-collapse>
  </b-navbar>
</template>

<script>
import { mapGetters } from "vuex";
import config from "@/common/config";
import DropDownMenu from "@/components/DropDownMenu.vue";
import TreeMenu from "@/components/TreeMenu.vue";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faHome,
  faSignInAlt,
  faSignOutAlt,
} from "@fortawesome/free-solid-svg-icons";
import { faListAlt } from "@fortawesome/free-regular-svg-icons";

library.add(faHome, faSignInAlt, faSignOutAlt, faListAlt);

export default {
  name: "MyHeader",
  data() {
    return {
      // DropDownMenu >>>
      menuData: config.menuData,
      btnClass: config.btnClass,
      btnID: config.btnID,
      // DropDownMenu <<<
      list: config.treeMenu,
      form: {
        search: "",
      },
    };
  },
  components: {
    DropDownMenu,
    TreeMenu,
  },
  mounted() {
    // console.log("MyHeader");
    // console.log(btnClass);
  },
  computed: {
    productItems() {
      return this.$store.getters["cart/cartProducts"].length;
    },
    getUserChName() {
      return this.currentUser.chName;
    },
    ...mapGetters("auth", ["isAuthenticated", "currentUser"]),
  },
  methods: {
    refresh() {
      this.$store.dispatch("products/refresh");
      this.$store.dispatch("cart/refresh");
    },
    logout() {
      // https://bootstrap-vue.org/docs/components/modal#confirm-message-box
      this.$bvModal
        .msgBoxConfirm("請確認是否登出?", {
          title: "登出",
          size: "md",
          buttonSize: "md",
          okVariant: "danger",
          okTitle: "是",
          cancelTitle: "否",
          footerClass: "p-2",
          hideHeaderClose: false,
          centered: true,
        })
        .then((value) => {
          console.log("logout : ", value);
          if (value) {
            this.$store.dispatch("auth/logout");
            this.$store.dispatch("deliver/clearState");
            this.$store.dispatch("complete/clearState");
            // Uncaught (in promise) NavigationDuplicated: Avoided redundant navigation to current location:
            // https://juejin.im/post/6865702641935384590
            this.$router
              .push({ name: "index" })
              .catch((err) => console.log("MyHeader.vue", err));
          }
        })
        .catch((err) => {
          console.log("msgBoxConfirm : ", err);
        });
    },
    onSubmit(event) {
      event.preventDefault();
      //alert(JSON.stringify(this.form));
      console.log(JSON.stringify(this.form));
    },
  },
};
</script>

